.shadow {
  box-shadow: 0 1px 3px rgb(0 0 0 / 12%), 0 1px 2px rgb(0 0 0 / 24%);
}

@keyframes dropHeader {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

#mobile-menu-open, #mobile-menu-close {
    display: none;
}

header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 10;
  animation-name: dropHeader;
  animation-iteration-count: 1;
  animation-timing-function: ease;
  animation-duration: 0.75s;
}

header ul {
  display: inline-block;
  background: #fff;
  text-align: center;
  padding: 10px;
  margin: 0;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
}

header li {
  display: inline-block;
  font-size: 20px;
}

header a {
  display: block;
  color: #3498db;
  padding: 10px;
  text-decoration: none;
}

header a:hover {
  color: #217dbb;
  text-decoration: none;
  background: #eee;
  border-radius: 4px;
}

header a:focus {
  color: #3498db;
  text-decoration: none;
}

header.active {
  display: block;
}

header.sticky {
  position: fixed;
  z-index: 999;
}

@media screen and (max-width: 768px){
#mobile-menu-open, #mobile-menu-close {
    display: none;
}
#mobile-menu-open {
    cursor: pointer;
    position: fixed;
    overflow: hidden;
    left: 10px;
    top: 10px;
    color: #3498db;
    font-size: 1.3em;
    z-index: 999;
    padding: 0 7px;
    border-radius: 4px;
}
.shadow-large {
    box-shadow: 0 3px 6px rgb(0 0 0 / 8%), 0 3px 6px rgb(0 0 0 / 15%);
    height: 40px;
}
header{
  display: none;
}
#menu {
    height: 100%;
    overflow-y: auto;
    box-shadow: none;
    border-radius: 0;
    width: 100%;
}
}
