.project{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0 0;
    overflow: hidden;
}

.project-browser{
    height: 20px;
    background-color: #949aa5;
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
}

.project-crcl{
    width: 6px;
    height: 6px;
    border-radius: 100%;
    margin: 3px;
    background-color: #fff;
}

.project-img{
    width: 100%;
    transition: all 10s ease;
}

.project:hover .project-img{
    transform: translateY(-100%);
}

@media screen and (max-width: 480px){
    .project{
        width:40%;
        height:20vh;
    }
}