.experience {
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.experience-text {
  width: 65%;
}

.experience-title {
  font-size: 46px;
  font-weight: 300;
  margin: 0 0 30px 0;
}

.experience-title:after {
    position: absolute;
    content: '';
    height: 2px;
    width: 70px;
    background: #3498db;
    margin-top: 65px;
    left: 48%;
}

@media screen and (max-width: 480px){
    .experience{
        padding: 10px;
    }
    .experience-title{
        font-size: 36px;
    }
    .experience-title:after {
        position: absolute;
        content: '';
        height: 2px;
        width: 70px;
        background: #3498db;
        margin-top: 55px;
        left: 40%;
    }
}
