.footer{
    position: relative;
    padding: 50px;
    color: #74808a;
    height: 3vh;
    display: flex;
}

h5{
    font-size: 18px;
    font-weight: 200;
}

.footer-left {
    flex: 1;
    position: absolute;
}

.footer-wrap {
    cursor: pointer;
    display: block;
    margin: 0 auto 0 auto;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    border: 3px solid #b9bfc4;
    text-align: center;
}
.footer-right{
    flex:1;
    right: 50%;
    position: absolute;
}

.chevron-up{
    width: 17px;
    bottom: 0;
    top: 0;
    right: 0;
    left: 0;
}

@media screen and (max-width: 480px){
    .footer{
        flex-direction: column;
        margin-top: 10px;
        bottom: 0;
        position: relative;
    }
    h5{
        font-size: 15px;
    }
    .footer-left{
        position:absolute;
        bottom: 0;
    }
    .footer-right{
        position:absolute;
        left: 45%;
    }
    .chevron-up{
        margin-top: 5px;
    }
}

