.project-l{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    /* background: #f2f2f5; */
}

.project-l-text{
    width: 65%;
}

.project-l-title{
    font-size: 46px;
    font-weight: 300;
    margin: 0 0 30px 0;
}

.project-l-title:after {
    position: absolute;
    content: '';
    height: 2px;
    width: 70px;
    background: #3498db;
    margin-top: 65px;
    left: 48%;
}

.project-l-desc{
    margin: 20px 0;
}

.project-l-list{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}


@media screen and (max-width: 480px){
    .project-l{
        padding: 10px;
    }
    
    .project-l-title{
        font-size: 36px;
    }
    .project-l-title:after {
        position: absolute;
        content: '';
        height: 2px;
        width: 70px;
        background: #3498db;
        margin-top: 55px;
        left: 40%;
    }

    .project-l-text{
        width: 100%;
    }

    .project-l-desc{
        display:none;
    }

}