.skills{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.skills-text {
  width: 65%;
}

.skills-title {
  font-size: 46px;
  font-weight: 300;
  margin: 0 0 30px 0;
}

.skills-title:after {
    position: absolute;
    content: '';
    height: 2px;
    width: 70px;
    background: #3498db;
    margin-top: 65px;
    left: 48%;
}

.skills-list ul {
  display: block;
  margin: 0 auto;
  padding: 0;
  max-width: 800px;
}

.skills-list li {
  display: inline-block;
  margin: 7px;
  padding: 5px 10px;
  color: #374054;
  background: #e4e4ea;
  list-style: none;
  cursor: default;
  font-size: 1em;
}

@media screen and (max-width: 480px){
  .skills{
    flex-direction:column;
    padding:0;
    margin-bottom: 30px;
  }

  .skills-list{
    font-size: 18px;
  }

  .skills-title{
    font-size:36px;
  }
  .skills-title:after {
        position: absolute;
        content: '';
        height: 2px;
        width: 70px;
        background: #3498db;
        margin-top: 55px;
        left: 40%;
    }

}