.contact {
   height: 78vh; 
   position: relative;
   /* background: #f2f2f5; */
}

.contact-bg{
    width: 20px;
    height: 100%;
    background-color: #3498db;
    position: absolute;
}

.contact-wrapper{
    padding: 80px;
    display: flex;
}

.contact-left{
    flex: 1;
}

.contact-right{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contact-title {
  font-size: 46px;
  font-weight: 300;
  margin: 0 0 30px 0;
}

.contact-title:after {
    position: absolute;
    content: '';
    height: 2px;
    width: 70px;
    background: #3498db;
    margin-top: 65px;
    left: 9%;
}

.contact-info-item{
    display: flex;
    align-items: center;
    margin: 50px 0;
    font-size: 20px;
    font-weight: 300;
    width: 70%;
}

.contact-icon{
    margin-right: 20px;
    color: #3498db;
}

.form-control{
    margin-top: 20px;
}

.input-form{
    width: 100%;
    height: 50px;
    border: none;
    border-bottom: 1px solid rgb(90, 90, 90);
    margin: 10px 0;
    font-size: 20px;
    padding-left: 10px;
    background-color: transparent;
}

.textarea-form{
    width: 100%;
    margin: 10px 0;
    font-size: 18px;
    padding-left: 10px;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid rgb(90, 90, 90);
}

.btn{
    border: none;
    padding: 10px;
    color: white;
    font-weight: 400;
    cursor: pointer;
    font-size: 18px;
    border-radius: 5px;
}

.btn-submit{
    background-color: #3498db;
}

.email-sent{
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
    margin-left: 10px;
    font-size: 15px;
    padding: 15px;
}
a{
    text-decoration: none !important;
    color: #111;
}

@media screen and (max-width: 480px){
    
    .contact{
        height: 100%;
    }
    .contact-wrapper{
        flex-direction: column;
        padding: 0 50px;
    }

    .contact-title{
        font-size:36px;
    }
    .contact-title:after {
        position: absolute;
        content: '';
        height: 2px;
        width: 70px;
        background: #3498db;
        margin-top: 55px;
        left: 25%;
    }


    .contact-info-item{
        margin: 20px 0;
        width: 100%;
        font-size:18px;
        justify-content: left;    
    }

    .contact-icon{
        height:20px;
        width:20px;
    }

    .geo-i{
        height: 30px;
        width: 30px;
    }

    .form-control{
        margin-top: 0;
    }

    .input-form, 
    .textarea-form{
        width: 100%;
        height: 40px;
        margin: 10px;
        margin-left: 0;
        font-size:18px;
    }
    .btn-submit{
        margin-top:10px;
    }
}