.intro{
    display: flex;
    height: 100vh;
}

.intro-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.intro-right{
    flex: 1;
    position: relative;
}

.intro-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.intro-i-greet{
    font-size: 30px;
    font-weight: 300;
}

.intro-title{
    height: 50px;
    overflow: hidden;
    color: #3498db;
    font-weight: 400;
}

.intro-title-wrapper{
    height: 100%;
    animation: move 5s ease-in-out infinite alternate;
}

@keyframes move {
    50%{ 
        transform: translateY(-50px); 
    }
    100%{
        transform: translateY(-100px);
    }
}

.intro-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: 300;
    display: flex;
    align-items: center;
}

.intro-image{
    width: 100%;
    height: 109%;
    object-fit: contain;
    position: absolute;
}

.intro-bg-image{
    clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
    background-color: #3498db;
    background-image: linear-gradient(315deg, #3498db 0%, #08183a 74%);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

@media screen and (max-width: 480px){
    .intro{
        flex-direction: column;
    }

    .intro-left{
        margin-top: 30px;
    }

    .intro-name{
        /* white-space: normal; */
        /* margin: 10px; */
    }

    .intro-left-wrapper{
        padding:10px;
        align-items:center;
        justify-content:center;
        text-align:center;  
        height:100%; 
    }
    .intro-desc{
        display:none;
    }

    .intro-image{
        width: 100%;
        height: 95%;
        bottom: 0;
        object-fit: contain;
        position: absolute;
    }
    .intro-bg-image{
        clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
        background-color: #3498db;
        background-image: linear-gradient(315deg, #3498db 0%, #08183a 74%);
        width: 100%;
        height: 100%;
        position: absolute;
    }
    
}