.switch{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid #333;
    background-color: transparent;
    position: fixed;
    overflow: hidden;
    padding: 2px;
    top: 10px;
    right: 10px;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.switch-icon{
    width: 15px;
    height: 15px;
}

.switch-button{
    width: 26px;
    height: 26px;
    border-radius: 50%;
    background-color: #222;
    position: absolute;
    cursor: pointer;
}
.sun{
    color: #222;
}
.moon{
    color: #fff;
}