.about{
    height: 100vh;
    display: flex;
    align-items: center;
    /* background: #f2f2f5; */
}

.about-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.about-right{
    flex: 1;
}

.about-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #949aa5;
}

.about-card{
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}

.about-image{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.about-title{
    font-size: 46px;
    font-weight: 300;
    margin: 0 0 30px 0;
}

.about-title:after {
    position: absolute;
    content: '';
    height: 2px;
    width: 70px;
    background: #3498db;
    margin-top: 65px;
    left: 55%;
}

.about-sub-t{
    margin: 20px 0;
}

.about-desc{
    font-weight: 300;
}

@media screen and (max-width: 480px){
    .about{
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
        height: 120vh;
    }
    
    .about-title{
        font-size: 36px;
        margin-top: -30px;
    }

    .about-desc{
        font-size: 18px;
    }

    .about-left{
        width: 100%;
    }
    .about-card{
        height:40vh;
        width: 70%;
    }
    .about-title:after {
        position: absolute;
        content: '';
        height: 2px;
        width: 70px;
        background: #3498db;
        margin-top: 55px;
        left: 40%;
    }

    .about-card.bg{
        display:none;
    }
    
    .about-right{
        padding:20px;
    }
}